
.project-card--empty {
  width: 100%;
  height: 100px;
  margin-top: 70px;
  text-align: center;
  cursor: pointer;
}
.project-card--empty h3 {
    color: #d6d6d6;
    font-size: 36px;
    transition: all .3s;
}
.project-card--empty p {
    color: #d6d6d6;
    font-size: 18px;
    transition: all .3s;
}
.project-card--empty:hover h3 {
  color: #20A0FF;
}
.project-card--empty:hover p {
  color: #20A0FF;
}



